<template>
	<div id="app_wrapper">
		<div class="header_wrapper border-bottom" v-if="$route.meta.requiresAuth">
			<Header @logout="logout()"/>
		</div>
		<div class="main_body">
			<template v-if="$route.meta.requiresAuth">
				<router-view v-if="userId"/>
			</template>
			<template v-else>
				<router-view/>
			</template>
		</div>
	</div>
</template>

<script setup>
// COMPONENTS
import Header from "@/components/organism/header/Header-Comp.vue"

// PACKAGES
import { computed, onMounted, provide, watch } from "vue"
import {App} from "realm-web"
import {mongoConnection} from "@/utils/realm/dbConnector"
import { useStore } from "vuex";
import { DB_NAME } from "@/config/env";
import { useRouter } from "vue-router";
import { handleUser } from "./utils/composables/customComposable";

const app = new App({ id: process.env.VUE_APP_MONGO_APP_ID });

// HELPERS
const router = useRouter();
const {getters} = useStore();
const {getUserData} = handleUser();

const userData = computed(() => {
	return getters["users/user"]
});
const userId = computed(() => {
	return getters["users/user"]?._id
});
provide("userData", userData);
provide("userId", userId);

watch(userData, () => {
	checkUserAlreadyInLobby()
})

function checkUserAlreadyInLobby() {
	if(userData.value?.currentLobby) {
		router.push({name: "Lobby", params: {id: userData.value?.currentLobby}})
	}
}

onMounted(() => {
	if (app.currentUser !== null && app.currentUser !== undefined) {
		let user = app.currentUser
		if(user) {
			(async() => {
				await mongoConnection({database: DB_NAME}).catch((error) => console.error("ERROR: ", error));
				getData(user.id);
			})()
		}
	}
})

function getData(uid) {
	getUserData(uid)
}

function logout() {
	if(app.currentUser) {
		app.currentUser.logOut();
	}
}
</script>

<style lang="css">
#app_wrapper {
	height: 100vh;
	width: 100vw;
}
body {
	margin: 0!important;
}

.header_wrapper{
	height: 45px;
	width: 100%;
}

.main_body {
	height: calc(100% - 77px);
	width: calc(100% - 30px);
	overflow: auto;
	padding: 15px;
}
</style>