import * as mutations from "./mutations"
import * as actions from "./actions"

const state = {
    user: {},
    friends: [],
    invites: [],
    requests: []
}

const getters = {
    user: state => state.user,
    invites: state => state.invites,
    requests: state => state.requests,
    friends: state => {
        return state.friends;
    }
}

export default {
    namespaced: true,
    state: state,
    getters: getters,
    mutations: mutations,
    actions: actions
}