import { useStore } from "vuex";

export function useCustomComposable() {
    function setTitle({title = "", prefix = ""}) {
        document.title = prefix + title;
    }

    function debounce(func, timeout = 300) {
        let timer = null;
        return (...args) => {
            clearTimeout(timer);
            timer = setTimeout(() => { func.apply(null, args); }, timeout);
        };
    }

    function makeUniqueId(length = 6) {
        var result           = '';
        var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        var charactersLength = characters.length;
        for ( var i = 0; i < length; i++ ) {
            result += characters.charAt(Math.floor(Math.random() * 
            charactersLength));
        }
        return result;
    }

    return {
        setTitle,
        makeUniqueId,
        debounce
    }
}

export function handleUser() {
    const {getters, dispatch} = useStore();

    function getUserData(uid) {
        try {
            if(getters['users/user'] && !Object.keys(getters['users/user'] || {}).length){
                dispatch('users/getUser', {uid})
                .catch((error) =>{
                    console.error('ERROR in set user data', error)
                })
            }
            if(getters['users/friends'] && !Object.keys(getters['users/friends'] || {}).length){
                dispatch('users/getFriends', {uid})
                .catch((error) =>{
                    console.error('ERROR in set user data', error)
                })
            }
            if(getters['users/invites'] && !Object.keys(getters['users/invites'] || {}).length){
                dispatch('users/getInvites', {uid})
                .catch((error) =>{
                    console.error('ERROR in set user data', error)
                })
            }
            if(getters['users/requests'] && !Object.keys(getters['users/requests'] || {}).length){
                dispatch('users/getRequests', {uid})
                .catch((error) =>{
                    console.error('ERROR in set user data', error)
                })
            }
        } catch (error) {
            console.error("ERROR in get user data");
        }
    }

    return {
        getUserData
    }
}