export function mutateUser(state, payload) {
    const {data, op} = payload;

    if(op === "delete") {
        state.user = {}
    } else {
        state.user = data
    }
}

export function mutateFriends(state, payload) {
    const {data, op} = payload;

    const friendIndex = state.friends?.findIndex((x) => x._id === data._id);
    if(op === "added") {
        if(friendIndex !== -1) {
            state.friends[friendIndex] = data;
        } else {
            state.friends.push(data);
        }
    } else if(op === "update") {
        if(friendIndex !== -1) {
            state.friends[friendIndex] = data;
        } else {
            state.friends.push(data);
        }
    } else {
        if(friendIndex !== -1) {
            state.friends.splice(friendIndex, 1);
        }
    }
}

